* {
  margin: 0;
  padding: 0;
  font-family: "Redressed", cursive;
  overflow-x: hidden;
  background-color: #1a1a1a;
}
.navbar {
  width: 100vw;
  height: auto;
  padding: 2vh 0vw;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.wel {
  display: none;
}
.navbar ul {
  list-style: none;
  font-size: 2vw;
  text-align: center;
}
.navbar ul li {
  display: inline-block;
  color: white;
  margin: 0px 4vw;
  cursor: pointer;
}
.list_burger {
  display: none;
}
.img {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
  margin-top: 10vh;
}
.texts {
  flex: 1;
}
.texts p {
  position: absolute;
  color: rgb(83, 83, 83);
  font-size: 8vw;
  background-color: transparent;
  animation: onload 0.6s ease-in-out 1 forwards;
}
@keyframes onload {
  from {
    top: 0%;
    left: 0%;
  }
  to {
    top: 20%;
    left: 7%;
  }
}
.photo {
  flex: 1;
}
.photo img {
  width: 100%;
  height: 100%;
  opacity: 1;
}

.title {
  padding-top: 40px;
  text-align: center;
  font-size: 6vw;
  color: #1a1a1a;
  text-shadow: -1px 0px 6px gray;
  padding-bottom: 40px;
}
.topic {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 40px 0px;
}
.top1 {
  width: 40vw;
  height: 60vh;
  background-image: url(/src/component/imgs/Capture.PNG);
  background-size: 100% 100%;
  text-align: center;
  border-radius: 9px;
}
.para {
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  transition: opacity 0.3s;
}
.para h2, .para p {
  padding: 3px 3px;
  color: white;
  font-weight: 900;
  background-color: transparent;
}
.github{
  margin: 10px 0px;
  padding: 10px;
  width: 10vw;
  height: auto;
  border-radius: 9px;
}
.github a{
  text-decoration: none;
  color: white;
}

.para:hover {
  opacity: 1;
}
.top2 {
  width: 40vw;
  height: 60vh;
  background-image: url(/src/component/imgs/news.jpg);
  text-align: center;
  border-radius: 9px;
  background-size: cover;
}
.mainskills {
  height: auto;
}
.skills {
  text-align: center;
  font-size: 6vw;
  color: #1a1a1a;
  text-shadow: -1px 0px 6px gray;
  padding-bottom: 40px;
}
.skill-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}
.skill-card {
  position: relative;
  width: 30vw;
  height: auto;
  margin-bottom: 20px;
}
.skill-card img {
  width: 65%;
  margin: 0px 17.5%;
}
.skill-name {
  color: white;
  font-size: 3vw;
  font-weight: 300;
  text-align: center;
  text-transform: capitalize;
  margin: 30px 0 20px;
}
.skill-des {
  color: white;
  text-align: center;
  opacity: 0.5;
  font-size: 2vw;
}
.skill-level {
  position: absolute;
  bottom: 42%;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 10vw;
  height: 10vw;
  border-radius: 50%;
  border: 10px solid;
}
.skill-card:nth-child(1) .skill-level {
  background: #ff4f4f28;
  border-color: #ff4f4f;
  color: #ff4f4f;
}

.skill-card:nth-child(2) .skill-level {
  background: #4fa0ff28;
  border-color: #4fa0ff;
  color: #4fa0ff;
}

.skill-card:nth-child(3) .skill-level {
  background: #ffed4f28;
  border-color: #ffed4f;
  color: #ffed4f;
}

.skill-card:nth-child(4) .skill-level {
  background: #52ff4f28;
  border-color: #52ff4f;
  color: #52ff4f;
}

.skill-card:nth-child(5) .skill-level {
  background: #4fdfff28;
  border-color: #4fdfff;
  color: #4fdfff;
}
.maincontact {
  margin: 20px;
}
.contact_heading {
  text-align: center;
  font-size: 6vw;
  color: #1a1a1a;
  text-shadow: -1px 0px 6px gray;
  padding-bottom: 40px;
}
.contact {
  width: 90vw;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.contact-form {
  display: flex;
  flex-direction: column;
}
.contact-form input,
textarea {
  width: 35vw;
  padding: 2vh 1vw;
  margin: 13px 0px;
  border-radius: 4px;
  border: 2px solid gray;
  background-color: gray;
}
::placeholder {
  color: black;
}

.form-submit-btn {
  color: white;
  width: 10vw;
  height: 5vh;
  margin: 30px auto 0px auto;
  background-color: #ff4f4f;
  border: 2px solid #ff4f4f;
  border-radius: 4px;
}

.map {
  overflow: hidden;
}
.map iframe {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 700px) {
  .skills {
    font-size: 6vh;
  }
  .title {
    font-size: 5vh;
  }
  .wel {
    display: inline-block;
    color: white;
    font-size: 5vw;
  }
  .navbar ul {
    display: none;
  }
  .changes .navbar ul {
    display: block;
  }
  .navbar ul li {
    display: block;
    margin: 4vw 0px;
    font-size: 5vw;
  }
  .list_burger {
    display: inline-block;
    cursor: pointer;
    position: absolute;
    top: 15px;
    right: 2vw;
  }
  .bar1,
  .bar2,
  .bar3 {
    margin-bottom: 8px;
    width: 35px;
    height: 5px;
    background-color: gray;
    transition: 0.4s;
  }
  .changes .bar1 {
    transform: rotate(-45deg) translate(-10px, 6px);
  }

  .changes .bar2 {
    opacity: 0;
  }

  .changes .bar3 {
    transform: rotate(45deg) translate(-11px, -8px);
  }
  .photo {
    width: 90vw;
    height: 40vh;
    margin: auto;
  }
  .img {
    margin-top: 6vh;
    display: flex;
    height: 40vh;
  }
  .texts {
    flex: 0;
  }
  .photo img {
    opacity: 1;
  }
  @keyframes onload {
    from {
      top: 0%;
      left: 0%;
    }
    to {
      top: 30%;
      left: 7%;
    }
  }
  .topic {
    flex-direction: column;
  }
  .top1,
  .top2 {
    width: 90vw;
    height: 40vh;
    margin-bottom: 20px;
  }
  .skill-flex {
    flex-direction: column;
  }
  .skill-card {
    width: 90vw;
    /* height: 70vh; */
  }
  .skill-name {
    font-size: 10vw;
  }
  .skill-des {
    font-size: 5vw;
  }
  .skill-level {
    width: 10vh;
    height: 10vh;
    bottom: 25vh;
    left: 0vw;
  }
  .maincontact {
    margin: 0px;
  }
  .contact_heading {
    font-size: 6vh;
  }
  .contact {
    flex-direction: column;
  }
  .contact-form input,
  textarea {
    width: 90vw;
  }
  .form-submit-btn {
    width: 60vw;
  }
  .map {
    margin: 30px 0px;
  }
}
